/**
 * The following styles get applied inside the editor only.
 *
 * Replace them with your own styles or remove the file completely.
 */

.wp-block-create-block-vayu-blocks {
	border: 1px dotted #f00;
}

.editor-styles-wrapper .wp-block-vayu-blocks-advance-heading{
	
	--background:none;
	--background-hover: var(--background);
	--transition:0.2s;
	--boxShadow-hvr:var(--boxShadow);
	--background-attachment-hvr:var(--background-attachment);
	--background-position-hvr:var(--background-position);
	--background-repeat-hvr:var(--background-repeat);
	--background-size-hvr:var(--background-size);
	--border-top-width-hvr:var(--border-top-width);
	--border-bottom-width-hvr:var(--border-bottom-width);
	--border-right-width-hvr:var(--border-right-width);
	--border-left-width-hvr:var(--border-left-width);
	--border-top-right-radius-hvr:var(--border-top-right-radius);
	--border-top-left-radius-hvr:var(--border-top-left-radius);
	--border-bottom-right-radius-hvr:var(--border-bottom-right-radius);
	--border-bottom-left-radius-hvr:var(--border-bottom-left-radius);
	--heading-border-hvr-style:var(--heading-border-style);
	--heading-border-hvr-color:var(--heading-border-color);
	--heading-hvr-color:var(--heading-color);

	font-family:var(--heading-font-family);
	font-weight:var(--heading-font-weight);
	font-style:var(--heading-font-style);
	text-transform:var(--heading-text-transform);
	text-align:var(--text-align);
	color:var(--heading-color);
	background: var(--background);
	background-repeat: var(--background-repeat);
	background-size: var(--background-size);
	background-position: var(--background-position);
	background-attachment: var(--background-attachment);
	padding-top:var(--padding-top);
	padding-right:var(--padding-right);
	padding-bottom:var(--padding-bottom);
	padding-left:var(--padding-left);
	margin-top:var(--margin-top);
	margin-bottom:var(--margin-bottom);
	border-style:var(--heading-border-style);
	border-color:var(--heading-border-color);
	border-top-width:var(--border-top-width);
	border-bottom-width:var(--border-bottom-width);
	border-right-width:var(--border-right-width);
	border-left-width:var(--border-left-width);
	border-top-right-radius:var(--border-top-right-radius);
	border-top-left-radius:var(--border-top-left-radius);
	border-bottom-right-radius:var(--border-bottom-right-radius);
	border-bottom-left-radius:var(--border-bottom-left-radius);
	box-shadow:var(--boxShadow);
	z-index:var(--z-index);
	font-size:var(--font-size);
    line-height:var(--line-height);
	letter-spacing:var(--letter-spacing);
    width:var(--width);
	max-width:var(--max-width);
	&:hover {
		color:var(--heading-hvr-color);
		background: var(--background-hover);
		background-repeat: var(--background-repeat-hvr);
		background-size: var(--background-size-hvr);
		background-position: var(--background-position-hvr);
		background-attachment: var(--background-attachment-hvr);
		border-style:var(--heading-border-hvr-style);
	    border-color:var(--heading-border-hvr-color);
		box-shadow:var(--boxShadow-hvr);
		border-top-width:var(--border-top-width-hvr);
		border-bottom-width:var(--border-bottom-width-hvr);
		border-right-width:var(--border-right-width-hvr);
		border-left-width:var(--border-left-width-hvr);
		border-top-right-radius:var(--border-top-right-radius-hvr);
		border-top-left-radius:var(--border-top-left-radius-hvr);
		border-bottom-right-radius:var(--border-bottom-right-radius-hvr);
		border-bottom-left-radius:var(--border-bottom-left-radius-hvr);
        transition:var(--transition);
	}
} 


@media only screen and (min-width: 1024px)  {
	.wp-block-vayu-blocks-advance-heading.th-hide-desktop:before {
		content: "";
		display: block;
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: hsla(0,0%,100%,.6);
		z-index:99999999;
		
	}

	.block-editor-block-list__layout .block-editor-block-list__block.th-hide-desktop.wp-block-vayu-blocks-advance-heading{
	 position: initial;
	pointer-events:none;
	}
	
}


@media only screen and (min-width: 768px) and (max-width: 1023px) {
	.wp-block-vayu-blocks-advance-heading.th-hide-tablet:before {
