		background-color: hsla(0,0%,100%,.6);
		z-index:99999999;
		
	}

	.block-editor-block-list__layout .block-editor-block-list__block.th-hide-mobile.wp-block-vayu-blocks-advance-heading{
	 position: initial;
	pointer-events:none;
	}

}